import styled from "styled-components";
import { css } from "styled-components";

import { useState } from "react";

import StarImg from "../../assets/images/StarImg.svg";
import { A, Text, Title3 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonDesktop";
import CheckBox from "./CheckBox";
import { EngLoc } from "../../assets/constants/localizations";

const SForm = styled.form`
  display: flex;
  padding: 80px;
  margin: 0 15%;
  position: relative;
  border-radius: 53px;
  overflow: hidden;

  & div:last-child {
    display: flex;
    gap: 32px;
    text-align: left;
  }

  & button {
    width: 100%;
    padding: 0;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 60px;
  }

  &::before {
    z-index: 0;
    background: #000f67;
  }

  &::after {
    z-index: 1;
    overflow: hidden;
    background: radial-gradient(
        8.2% 16.75% at 87.23% 33.31%,
        #3308e1 0%,
        rgba(51, 8, 225, 0) 100%
      ),
      radial-gradient(
        10.67% 22.58% at 76.53% 50.06%,
        #5ab0ff 0%,
        rgba(90, 176, 255, 0) 100%
      ),
      radial-gradient(
        18.93% 58.28% at 52.57% -8.22%,
        #00aee8 0%,
        rgba(0, 174, 232, 0) 100%
      ),
      radial-gradient(
        46.63% 94.35% at 25.84% -44.29%,
        #00aee8 0%,
        rgba(0, 174, 232, 0) 100%
      ),
      radial-gradient(
        48.87% 44.66% at 92.43% -22.27%,
        #ff7cda 0%,
        rgba(255, 124, 218, 0) 100%
      ),
      radial-gradient(
        38.2% 51.8% at 5.88% 109.91%,
        rgba(5, 237, 223, 0.82) 0%,
        rgba(5, 237, 223, 0) 100%
      ),
      radial-gradient(
        34.97% 74.71% at 66.33% 100%,
        rgba(5, 237, 223, 0.63) 0%,
        rgba(5, 237, 223, 0) 100%
      ),
      radial-gradient(
        49.55% 85.22% at 27.07% 80.55%,
        #7400e8 0%,
        rgba(116, 0, 232, 0) 100%
      ),
      radial-gradient(
        72.94% 127.04% at 50% 50%,
        rgba(0, 174, 232, 0.53) 0%,
        rgba(0, 113, 245, 0.506076) 36.28%,
        rgba(0, 174, 232, 0) 100%
      );
    filter: blur(72.74px);
  }

  @media (max-width: 1200px) {
    margin: 0 10%;
  }
`;

const FormBox = styled.div`
  z-index: 2;
  display: flex;
  flex-direction: column;
  max-width: 800px;
  justify-content: space-between;
  text-align: left;
`;

const Div = css`
  display: flex;
  flex-direction: column;
`;

const TitleBox = styled.div`
  ${Div}
`;

const InputsBox = styled.div`
  ${Div}

  & div {
    display: flex;
    gap: 64px;
  }

  margin-bottom: 40px;
`;

const Img = styled.img`
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
  width: 35%;
  pointer-events: none;
  user-select: none;
`;

const Title = styled(Title3)`
  margin-bottom: 24px;
  font-weight: 300;
  line-height: 130%;
`;

const P = styled(Text)``;

const Input = styled.input`
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  height: 80px;
  max-width: 400px;
  border-radius: 16px;
  margin-bottom: 15px;
  padding-left: 24px;
  margin-top: 60px;
  outline: none;

  &::placeholder,
  & {
    font-family: "Inter";
    font-weight: 300;
    font-size: 20px;
    line-height: 150%;
    color: rgba(255, 255, 255, 0.8);
  }

  &:focus {
    outline: 2px solid rgba(255, 255, 255, 0.3);
  }
`;

const TextArea = styled.div`
  position: relative;

  & p {
    position: absolute;
    top: 29px;
    font-weight: 300;
    font-size: 20px;
    left: 190px;
    color: #ffffff66;
  }
`;

const Textarea = styled.textarea`
  width: 100%;
  outline: none;
  padding: 24px;
  margin-bottom: 25px;
  margin-right: 42px;
  height: 156px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  resize: none;

  position: relative;

  &::placeholder,
  & {
    font-family: "Inter";
    font-weight: 300;
    font-size: 20px;
    line-height: 150%;
    color: #ffffffcc;
  }

  &:focus {
    outline: 2px solid rgba(255, 255, 255, 0.3);
  }
`;

const Link = styled(A)`
  color: #90e3ff;
  font-size: 16px;

  &:hover {
    color: #abe4f7;
  }
`;

const RequestForm = ({ language }) => {
  const [value, setValue] = useState("");
  const [valueTextArea, setValueTextArea] = useState("");

  return (
    <SForm id={"form"}>
      <FormBox>
        <TitleBox>
          <Title>
            {language === "Ru"
              ? "Хотите запустить рекламу?"
              : EngLoc.social_media.form.title}
          </Title>
          <P size="26">
            {language === "Ru"
              ? "Отправьте заявку и мы сделаем всё красиво"
              : EngLoc.social_media.form.p}
          </P>
        </TitleBox>

        <InputsBox>
          <Input
            placeholder={
              language === "Ru"
                ? "Ваш номер или мессенджер"
                : EngLoc.social_media.form.input
            }
            onChange={(e) => setValue(e.target.value)}
          />
          <TextArea>
            {valueTextArea.length === 0 && (
              <Text>
                {language === "Ru"
                  ? "(опционально)"
                  : EngLoc.social_media.form.textarea_after}
              </Text>
            )}
            <Textarea
              placeholder={
                language === "Ru"
                  ? "Опишите задачу"
                  : EngLoc.social_media.form.textarea
              }
              onChange={(e) => {
                setValueTextArea(e.target.value);
              }}
            ></Textarea>
          </TextArea>
          <div>
            <CheckBox
              name="call"
              text={
                language === "Ru"
                  ? "Позвоните мне"
                  : EngLoc.social_media.form.checkboxes[0]
              }
            />
            <CheckBox
              name="send_message"
              text={
                language === "Ru"
                  ? "Напишите мне в Телеграм"
                  : EngLoc.social_media.form.checkboxes[1]
              }
            />
          </div>
        </InputsBox>

        <div>
          <Button>
            {language === "Ru" ? "Отправить" : EngLoc.buttons.send}
          </Button>
          {language === "Ru" ? (
            <Text size="16">
              Нажимая на кнопку, вы даете согласие на{" "}
              <Link href="">обработку персональных данных</Link> и соглашаетесь
              c<Link href=""> политикой конфиденциальности</Link>
            </Text>
          ) : (
            <Text size="16">
              By clicking on the button, you consent to{" "}
              <Link href="">the processing of personal data</Link> and agree to
              <Link href=""> the privacy policy</Link>
            </Text>
          )}
        </div>
      </FormBox>
      <Img loading="lazy" src={StarImg} />
    </SForm>
  );
};

export default RequestForm;
