import styled from "styled-components";

import RBK from "../../assets/images/RBK.svg";
import Interfacs from "../../assets/images/interfacs.svg";
import Moscow24 from "../../assets/images/Moscow24_logo.svg";
import Mark from "../../assets/images/mark.svg";

const SRunLine = styled.div`
  height: 150px;
  width: 100%;
  background: #f4f8fc;
  margin-bottom: 180px;
  display: flex;
  align-items: center;
  gap: 50px;
  overflow: hidden;
  user-select: none;
  pointer-events: none;
`;

const LogosContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;

  animation: run-text 20s infinite linear;

  @keyframes run-text {
    0% {
      transform: translate(0, 0);
    }

    100% {
      transform: translate(calc(-100% - 50px), 0);
    }
  }
`;

const LogoBox = styled.div`
  display: flex;
  gap: 12px;
  height: 50px;
`;

const Logo = styled.img`
  height: 50px;
`;

const MarkImg = styled.img`
  width: 24px;
  height: 24px;
`;

const RunLine = () => {
  const Logos = [
    <Logo loading="lazy" src={RBK} />,
    <Logo loading="lazy" src={Moscow24} style={{ filter: "contrast(0.5)" }} />,
    <Logo loading="lazy" src={Interfacs} style={{ height: 80, marginTop: -15 }} />,
  ];

  return (
    <SRunLine>
      <LogosContainer>
        {Logos.map((logo) => (
          <LogoBox key={Math.random()}>
            <MarkImg loading="lazy" src={Mark} />
            {logo}
          </LogoBox>
        ))}
      </LogosContainer>
      <LogosContainer>
        {Logos.map((logo) => (
          <LogoBox key={Math.random()}>
            <MarkImg loading="lazy" src={Mark} />
            {logo}
          </LogoBox>
        ))}
      </LogosContainer>
      <LogosContainer>
        {Logos.map((logo) => (
          <LogoBox key={Math.random()}>
            <MarkImg loading="lazy" src={Mark} />
            {logo}
          </LogoBox>
        ))}
      </LogosContainer>
      <LogosContainer>
        {Logos.map((logo) => (
          <LogoBox key={Math.random()}>
            <MarkImg loading="lazy" src={Mark} />
            {logo}
          </LogoBox>
        ))}
      </LogosContainer>
    </SRunLine>
  );
};

export default RunLine;
